.wave:before,
.wave:after {
  content: "";
  position: absolute;
  width: 200%;
  height: 200%;
  top: 0;
  left: 50%;
  transform: translate(-50%, -75%);
  background: #000;
}
.wave:before {
  border-radius: 45%;
  background: rgba(255, 255, 255, 1);
  -webkit-animation: animate 5s linear infinite;
          animation: animate 5s linear infinite;
}
.wave:after {
  border-radius: 40%;
  /* background: rgba(255, 255, 255, -5);*/
  -webkit-animation: animate 5s linear infinite;
          animation: animate 5s linear infinite;
}

