/*STart of Magnetic Stirrer*/
.box {
  position: absolute;
  display: block;
  width: 18%;
  height: 5%;
  top: 53px;
  right: 34%;
  background: #7d7979;
  border-radius: 11px 11px 11px 11px;
  -webkit-animation: animates 2.8s linear infinite;
          animation: animates 2.8s linear infinite;
}

@-webkit-keyframes animates {
  0% {
    transform: rotateY(0deg);
  }
  100% {
    transform: rotateY(360deg);
  }
}

@keyframes animates {
  0% {
    transform: rotateY(0deg);
  }
  100% {
    transform: rotateY(360deg);
  }
}
/*End of Magnetic stirrer*/

@-webkit-keyframes animate {
  0% {
    transform: translate(-50%, -73%) rotate(0deg);
  }
  50% {
    transform: translate(-50%, -74%) rotate(180deg);
  }
  100% {
    transform: translate(-50%, -75%) rotate(360deg);
  }
}

@keyframes animate {
  0% {
    transform: translate(-50%, -73%) rotate(0deg);
  }
  50% {
    transform: translate(-50%, -74%) rotate(180deg);
  }
  100% {
    transform: translate(-50%, -75%) rotate(360deg);
  }
}

@-webkit-keyframes drip {
  to {
    top: 390px;
    opacity: 1;
    transition: opacity 0.1s;
  }
}

@keyframes drip {
  to {
    top: 390px;
    opacity: 1;
    transition: opacity 0.1s;
  }
}

