@import url(https://fonts.googleapis.com/css?family=Noto+Sans:400,700,400italic,700italic);

.team {
  background-color: azure;
}

.card {
  background-color: #fff;
  height: 300px;
  width: 250px;
  position: relative;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
  overflow: hidden;
}

.card:hover {
  box-shadow: 0px 20px 20px rgba(0, 0, 0, 0.2);
}

.card_circle {
  height: 400px;
  width: 450px;
  background: url(http://widewallpaper.info/wp-content/uploads/2015/11/Nature-HD-Wallpaper-1080p.jpg)
    no-repeat;
  background-size: cover;
  position: absolute;
  border-radius: 50%;
  margin-left: -75px;
  margin-top: -270px;
}

/* .card:hover .card_circle {
  border-radius: 0;
  margin-top: -130px;
} */

.card h2 {
  text-align: center;
  margin-top: 200px;
  position: absolute;
  z-index: 9999;
  font-size: 20px;
  color: #2196f3;
  width: 100%;
  font-weight: bold;
  font-family: "Noto Sans", serif;
}

.card small {
  height: 65px;
  width: 100%;
  padding: 15px;
  /* background-color: #eee; */
  font-weight: normal;
  position: absolute;
  bottom: 0;
  color: rgba(0, 0, 0, 0.5);
}

.card small a {
  text-decoration: none;
  color: #000;
  margin: 0 15px;
}
/* .card:hover h2 {
  margin-top: 100px;
  color: #fff;
}

.card:hover h2 small {
  color: #fff;
} */

.cta-container {
  text-align: center;
  margin-top: 290px;
  position: absolute;
  z-index: 9999;
  width: 100%;
  font-family: "Noto Sans", serif;
}

.cta {
  color: #fff;
  border: 2px solid #2196f3;
  background-color: #2196f3;
  padding: 10px 25px;
  border-radius: 50px;
  font-size: 17px;
  text-decoration: none;
  font-weight: bold;
}

/* .card:hover .cta-container {
  margin-top: 320px;
} */

.pic {
  width: 175px;
  height: 175px;
  margin: 20px;
}
.pic img {
  border-radius: 50%;
}
